}
#citySearch ul {
    background-image: -webkit-linear-gradient(top, whitesmoke, #dadada);
    background-image: linear-gradient(to bottom, whitesmoke, #dadada);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    margin-top: 9.9em;
    text-align: left;
}
#citySearch ul li {
      padding: 7.5px;
      padding-left: 30px;
}
#citySearch ul li .popup-city-city {
        display: inline-block;
        font-size: 32px;
}
