        color: #929292;
}
#citySearch ul li:hover {
      background-color: rgba(0, 0, 0, 0.1);
}
#citySearch .closeHolder {
  position: fixed;
  top: 7.5px;
  right: 7.5px;
  display: none;
}
#citySearch .closeCitySearch {
  width: 30px;
  height: 30px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
#citySearch .closeCitySearch:hover::before, #citySearch .closeCitySearch:hover::after {
    background: grey;
}
#citySearch .closeCitySearch::before, #citySearch .closeCitySearch::after {
    content: '';
    position: absolute;
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #000;
}
#citySearch .closeCitySearch::before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
#citySearch .closeCitySearch::after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
#citySearch.poppedUp {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.8);
}
#citySearch.poppedUp .closeHolder {
