    background-color: #fff;
    float: left;
    padding: 0 0 10px 30px;
    width: 100%;
}
#citySearch.poppedUp input {
    border: none;
    border-bottom: 1px solid #000;
    color: #000;
    padding: 3em 7.5px 10px 0px;
    float: left;
    width: 75%;
    font-size: 32px;
    background-color: white;
}
#citySearch.poppedUp *::-webkit-input-placeholder {
    color: #5a5a5a;
}
#citySearch.poppedUp *:-ms-input-placeholder {
    color: #5a5a5a;
}
#citySearch.poppedUp *::placeholder {
    color: #5a5a5a;
}
